import React from 'react';

const PsychologistPupils = () => {
  return (
    <React.Fragment>
      <div className="article">
        <article>
          <h5>Як готуватися до іспитів</h5>

          <p>Іспити, як і більшість явищ життя, – явище перехідне. Коли вони закінчуються, з’являється відчуття свободи, забуваються сльози, нервування, повертається спокійний сон. Але ж можна скласти іспит і з меншими втратами для власного здоров’я, отримати добрі оцінки й чекати наступного іспиту без страху та безсонних ночей.</p>
          <p>Психологи розцінюють іспит як:</p>

          <ul>
            <li>«перетравлювання» значного обсягу інформації, яке пов’язане з великим навантаженням на мозок;</li>
            <li>серйозне емоційне напруження і велике навантаження на нервову систему;</li>
            <li>оцінювання не лише знань дитини, а й її особистості.</li>
          </ul>

          <h5>Найоптимальніший варіант підготовки до складання іспитів:</h5>
          <p>Розпорядок дня:</p>
          <p>Харчуватися слід три-чотири рази на день. Мед, горіхи, молочні продукти, риба, м’ясо, овочі та фрукти забезпечать баланс необхідних вітамінів і мінералів в організмі й відновлять енергетичні витрати.</p>
          <p>Нормальний здоровий сон і відпочинок зменшать навантаження на нервову систему. Не варто відмовлятись од звичних занять спортом – це буде своєрідним відпочинком.</p>
          <p>Слід пам’ятати про біоритмо-логічні особливості: «сова» неефективно працює о 6 – 8 год ранку, а «жайворонок» навряд чи буде працездатним до 2 – 3 год. ночі. В усіх людей рівень біологічної активності знижений із 17 до 18 години. Каву краще пити вранці й небагато, лише для того, щоб прокинутися (для тих, хто звик саме так розпочинати свій день). Кава підсилює нервовість і тривожний стан у невпевнених собі людей. Заспокійливі медичні препарати зовсім не потрібні, їх можна вживати лише за призначенням лікаря.</p>
        
          <h5>Найоптимальніший варіант підготовки до складання іспитів:</h5>
          <p>Правильна організація вивчення навчального матеріалу</p>
          <p>Спочатку слід переглянути весь матеріал і розподілити його таким чином: якщо треба вивчити 100 питань за чотири дні, то в перші два дні вивчається 70% усього обсягу, третього дня – решта, четвертого – повторюють вивчене. При цьому складні теми варто чергувати з простішими – не обов’язково вчити всі питання поспіль.</p>
          <p>Є й деякі психологічні закономірності, які слід враховувати.</p>
          
          <ul>
            <li>Найкраще запам’ятовується інформація, викладена на початку і наприкінці тексту. Середина зазвичай швидко «вилітає» з голови. Тому під час запам’ятовування та повторення слід приділити увагу середині тексту.</li>
            <li>Повторювати треба не механічно, а вдумливо, зосередившись на змісті. Інформація після і «завантаження» в мозок, якщо її не повторити, утрачається на 20 – 30% упродовж перших10 год. Щоб цього не сталося, слід прочитати текст, повторити його вдруге, через 20 хв – утретє, а через 8 – 10 год. – учетверте, і ще раз – через добу. Тільки після цього можна бути впевненим у тому, що інформація міцно «оселилася» в голові.</li>
            <li>Особливість «упізнавання». Дивлячись на текст, відчуваєш, що колись це все бачив, читав, отож вирішуєш, що витрачати час на це питання не варто й можна переходити до наступного. Насправді ж це – несвідомий самообман. І якщо не вивчити уважно те, що здається таким знайомим, на іспиті можна не відповісти на таке «знайоме» запитання.</li>
            <li>Запам’ятати можна найкраще, якщо знати, що є «найближчим»: текст, малюнок, логічна схема.</li>
            <li>Слід спробувати знайти щось цікаве у найнуднішому матеріалі, адже цікаве запам’ятовується набагато легше.</li>
          </ul>

          <p>Можна готуватися до іспитів удвох, адже одна голова добре, а дві – краще. Підготовка до іспиту вдвох має свої переваги, якщо поставитися до цього серйозно й сумлінно. Учити разом не варто, а от повторювати навіть бажано. Це краще робити напередодні іспиту. Готуватися можна й так: перший день – самостійне вивчення, другий – повторення й переказування один одному. Було б добре розділити теми за інтересами, за складністю, за ступенем засвоєння: один розповідає те, що другий раніше не вивчив. Розповідати треба чітко, детально, емоційно. Можна проводити «репетиції» перед іспитом і наодинці: ставити собі запитання, уголос відповідати, привчатися слухати своє мовлення.</p>
          <p><b>Шпаргалка — річ потрібна.</b> Використання шпаргалок на іспиті найчастіше закінчується дуже сумно. Вони потрібні лише тим, хто краще запам’ятовує матеріал при конспектуванні, – викладений коротко (у вигляді тез), він краще тримається у пам’яті. Шпаргалки зручно перечитувати перед самим іспитом, а от брати на іспит не варто.</p>
          <p><b>«Спокійно, тільки спокійно»</b> Так завжди в екстремальних ситуаціях казав Карлсон. Слід скористатися його порадою. Адже нервують завжди ті, хто погано знає матеріал. Ті, хто зовсім нічого не знає, зазвичай спокійні. Ті, хто знає матеріал добре чи на середньому рівні, нажахані до останньої хвилини, поки не отримають оцінку. Щоб подолати свій страх, можна розповідати самому собі вивчений матеріал, начебто на іспиті. Уявіть складну ситуацію на іспиті й знайдіть із неї вихід. Поставте самі собі підступні запитання й дайте на них відповідь –переборіть свій страх ще до іспиту. Згадайте випадки свого успіху, подумки відчуйте їх іще раз. Спробуйте вдатися до аутогенного тренування. Налаштуйтеся на те, що «у мене все вийде» й повторюйте ці слова перед сном. На іспиті не втрачайте самовладання у складний момент, хоч це й нелегко. Згадайте про почуття гумору – іноді воно виявляється і в екзаменаторів.</p>
          <p>Складіть план відповіді на іспиті – це теж заспокоює.</p>
          <p>Бажаємо вам якнайкраще здати іспити!</p>

          <h5>Кілька корисних порад у спілкуванні.</h5>
          <ul>
            <li>Ніколи не поспішай робити категоричних висновків по першому враженню, особливо уникай негативних оцінок.</li>
            <li>Пам’ятай про те, що всі люди різні, кожна людина унікальна і не повторна. У житті не варто відносити людину до якогось типу, класифікувати людей.</li>
            <li>Ми часто ображаємося на людей або не задоволені ними, тому що вони не виправдовують наших очікувань, не містяться в ті рамки, які ми їм відвели. Але люди зовсім не зобов’язані бути такими, якими ми бажаємо їх уявляти.</li>
            <li>Пам’ятай про те, що будь-яка людина по своїй суті має унікальну здатність до зміни.</li>
            <li>Ніколи не плутай поведінку людини і її особистість. Оцінюючи вчинок людини, у жодному разі не вішай ярлик на її особистість.</li>
            <li>Навчися приймати будь-яку людину цілком, такою, якою вона є – з усіма її гідностями й недоліками.</li>
            <li>Як говорив Дейл Карнегі, якщо прагнеш переробити людей, почни із себе – це й корисно і безпечно.</li>
          </ul>

          <h5>Як управляти своїми емоціями.</h5>
          <ul>
            <li>Повністю позбуватися емоцій і не розумно, і неможливо. Немає емоцій поганих або гарних. На думку Жан-Жака Руссо, «усі страсті гарні, коли ми володіємо ними, і всі дурні, коли ми їм підкоряємося».</li>
            <li>Джонатан Свифт сказав: «Віддаватися гніву часто – однаково, що мстити самому собі за провину іншого». Обери ці слова своїм девізом, і ти станеш набагато більш врівноваженним і оптимістичним.</li>
            <li>Необхідна умова радісного настрою – гарні взаємини з людьми. І навпаки: радісне сприйняття життя – основа симпатії й розташування до тебе інших. Дай собі установку на добре й поважне відношення до людей. Прав Шекспір: «Дарма думати, начебто різкий тон є ознака прямодушності й сили».</li>
            <li>Не намагайся просто придушити в собі емоції. Подавлені почуття однаково виллються назовні тим або іншим способом, або можуть стати причиною захворювання. Краще відіграй ці емоції зовні, але без шкоди для навколишніх.</li>
            <li>Якщо в тебе виникло подразнення або злість, не борися з ними, а спробуй «відокремити» їх від себе. Поспостерігай за їхнім протіканням, знайди й проаналізуй причину їх появи, і ти переконаєшся, наскільки маленька й несерйозна ця причина. Усе це сприяє вгасанню негативних емоцій.</li>
            <li>У відносинах із близькими людьми часто буває потрібно відверто сказати про свої почуття, що виникли в тієї або іншій ситуації. Це набагато краще, чим фальш і закритість у спілкуванні. </li>
          </ul>

          <h5>ДЕСЯТЬ ПРАВИЛ УСПІХУ</h5>
          <ul>
            <li>Не сумуй! Той, хто сумує,  приречений на невдачі.</li>
            <li>Не бійся! Боягуз приречений на поразку.</li>
            <li>Трудися! Іншого шляху до успіху немає.</li>
            <li>Думай! Думай до вчинків, думай, зробивши вчинок, і навчишся не робити помилок.</li>
            <li>Не бреши! І ти будеш мати друзів. Не обманюй себе й Пам’ятай про психологічний захист.</li>
            <li>Навчися дивитися на себе й свої вчинки як би з боку, очими інших і ти багато чого зрозумієш.</li>
            <li>Залишайся завжди й у всім самим собою, іди своїм шляхом. У цьому випадку ти відбудешся як особистість і досягнеш бажаного результату. А якщо ні, то ти завжди будеш попутником і прийдеш до того, чого прагнуть інші, а не ти сам.</li>
            <li>Уникай злих людей, тому що зло заразливо.</li>
            <li>Будь вдячний батькам, бабусям, дідусям, людям, що зробили тобі добро.</li>
            <li>Невдячність – тяжкий гріх. Слухай старших, і ти уникнеш багатьох лих.</li>
          </ul>

          <h5>ПОРАДИ СТАРШОКЛАСНИКАМ</h5>
          <p>Відносини з батьками</p>

          <ul>
            <li>Проявляйте увагу до своїх батьків. Вони утомлюються на роботі. Порадуйте їх чим-небудь. Це допомагає налагодити контакт.</li>
            <li>Намагайтеся розмовляти з батьками людською мовою – це дасть кращий результат, ніж лементи й ляскіт дверми.</li>
            <li>Просіть батьків розповісти про свою юність, про конфлікти з батьками. Це приведе до більш глибокого розуміння між вами.</li>
            <li>Пам’ятайте, що іноді в батьків буває просто поганий настрій. Постарайтеся не влаштовувати в цей час у будинку гучних вечірок.</li>
            <li>Якщо батьки поводяться з вами, як з маленькою дитиною, нагадаєте чемно їм, що ви вже подорослішали й прагните разом з ними вирішувати проблеми, пов’язані з вашим життям.</li>
            <li>Дайте зрозуміти батькам, що ви потребуєте їхньої поради й допомоги. Це допоможе встановити у будинку сприятливу обставину.</li>
            <li>Розмовляйте зі своїми батьками, повідомляйте їм новини вашого життя.</li>
            <li>Намагайтеся не обманювати батьків. Таємне однаково стає явним.</li>
            <li>Пам’ятайте, що батьки теж можуть робити помилки. Будьте поблажливі.</li>
            <li>Батьківська заборона найчастіше виявляється правильним розв’язком. Задумайтеся, що б відбулося, якби вам було дозволено все.</li>
            <li>Якщо відбулася сварка й ви почуваєте себе винуватим, знайдіть у собі сили підійди першим. Повагу ви не втратите, повірте.</li>
            <li>Установите з батьками границі особистої території, але не дратуючись, не вимагаючи. Просто попрохайте їх про це.</li>
            <li>Робота в будинку служить відмінною підготовкою до самостійного життя. Віднесіться до неї з розумінням.</li>
            <li>Повернення додому до певного часу – це спосіб відгородити себе від неприємностей, тобто захід безпеки. Якщо затримуєтеся, обов’язково подзвоніть, вони ж хвилюються. Побережіть їх для себе.</li>
            <li>Про покарання. Батьки можуть припинити заохочення, відлучити вас від друзів. І все це може відбутися, як ви вважаєте, з – за дрібниці. Батьків же ця «дрібниця» може вивести із себе. Подумайте, може бути, батьки бачать небезпеку, яку ви або не усвідомлюєте, або зменшуєте.</li>
            <li>Вимагайте, щоб батьки пояснювали мотиви своєї поведінки стосовно вас і ваших друзів. Може бути, вам стане зрозуміло, чому вони чинять саме так.</li>
            <li>Якщо ви розізлилися на когось або із друзів, учителів, роздратовані на «двійку», поставлену « нізащо», не зривайте зло на батьках. Це не розсудливо й приводить до сумних наслідків.</li>
          </ul>

          <h5>ПСИХОЛОГІЧНІ РЕКОМЕНДАЦІЇ</h5>
          <ul>
            <li>Намагайся у всіх удачах дякувати обставинам, а у всіх невдачах винити тільки себе.</li>
            <li>Пам’ятай: немає безвихідних ситуацій. Ніколи не опускайте руки. Поки ми живемо, ми здатні поліпшити своє життя.</li>
            <li>Коли тобі погано, знайди того, кому ще гірше, і допоможи йому – тобі стане значно краще.</li>
            <li>Стався до інших людей так, як хотілося б щоб інші люди ставилися до тебе.</li>
            <li>Не обставини повинні управляти людиною, а людина обставинами.</li>
            <li>Вір у Його Величність Випадок і знай: випадок приходить тільки до тих, хто все робить для того, щоб цей випадок прийшов. Знай: якщо, проживши день, ти не зробив жодного доброї справи або за день не довідався нічого нового – день прожитий дарма.</li>
            <li>Саме коштовне в нашому житті – здоров’я. Про нього потрібно піклуватися. Спати стільки, скільки вимагає організм. Більше рухатися, раціонально харчуватися, оголосити війну палінню.</li>
            <li>Пам’ятайте: життя любить щасливих. Про минуле жалкувати даремно.</li>
            <li>Аналізуйте кожний прожитий день – вчитися найкраще на власних помилках.</li>
            <li>Кращі ліки від смутку, від стресу, від невдач – оптимізм.</li>
          </ul>
        </article>
      </div>
    </React.Fragment>
  );
};

export default PsychologistPupils;
